@import url("https://fonts.googleapis.com/css2?family=Asap:wght@400;700&family=Raleway:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Asap:wght@400;700&family=Raleway:wght@700&display=swap");
.ButtonModal {
  width: 20%;
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
}

.WideButton {
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
  width: 90%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/*Top menu*/
p, label {
  font-family: "Asap", sans-serif;
}

.MenuTripContainer {
  display: flex;
  align-items: flex-end;
  margin: 0 20px 0 20px;
}

.Item {
  width: 38%;
  border-radius: 5px 5px 0 0;
}

.Item p {
  font-weight: 700;
  font-size: 14px;
  color: #FFFFFF;
  text-align: center;
  vertical-align: middle;
}

.MenuTripItem1 {
  background-color: #005140;
  height: 42px;
  line-height: 42px;
}

.MenuTripItem2 {
  background-color: #00904A;
  height: 46px;
  line-height: 46px;
}

.Line {
  background-color: #00904A;
  height: 4px;
}

/*Filter*/
.Filter {
  width: 100%;
  margin-top: 10px;
  margin-bottom: 5px;
}

.Filter p {
  font-weight: bold;
  font-size: 14px;
}

.FilterArrow {
  display: flex;
}

.arrow {
  border: solid black;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 5px;
  margin-left: 12px;
  margin-bottom: 2px;
}

.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

.FilterContainer p {
  margin-bottom: 10px;
  font-weight: bold;
}

.FilterItem {
  width: 50%;
}

.FilterItem label {
  display: inline-block;
  padding: 5px;
  font-size: 12px;
  width: 80%;
}

input[type=checkbox] {
  width: 10%;
}

/*Ture body*/
.TripInfoContainer {
  margin: 0 20px 0 20px;
  display: flex;
  flex-wrap: wrap;
}

/*Tur info*/
.NewTrips {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 10px;
  flex-wrap: wrap;
}
.NewTrips .TripsHolder {
  width: 90%;
  display: grid;
  grid-template-columns: 1.1fr 2fr 0.7fr;
  grid-column: 1/-1;
  grid-template-rows: 1fr fr;
  color: #FDFDFD;
  font-family: "asap";
  box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.2);
}
.NewTrips .TripsHolder .Date {
  grid-column: 1/2;
  background-color: #005140;
  grid-row: 1/3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  border-radius: 0.25em 0 0 0.25em;
  padding: 5% 0 5% 0;
}
.NewTrips .TripsHolder .Date .Month {
  font-size: 20px;
  font-weight: normal;
}
.NewTrips .TripsHolder .Date .Number {
  font-size: 40px;
  font-weight: bold;
}
.NewTrips .TripsHolder .Info {
  grid-column: 2/3;
  background-color: #EAEAEA;
  grid-row: 1/3;
  color: #000000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5% 0 5% 5%;
  font-size: 14px;
  font-weight: normal;
  line-height: 20px;
}
.NewTrips .TripsHolder .Time {
  grid-column: 3/4;
  background-color: #00904A;
  grid-row: 1/2;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 0 0.25em 0 0;
  font-size: 14px;
  font-weight: normal;
}
.NewTrips .TripsHolder .Country {
  grid-column: 3/4;
  background-color: #313131;
  grid-row: 2/3;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 0 0 0.25em 0;
  font-size: 14px;
  font-weight: normal;
}
.NewTrips .MoreInfo {
  background-color: #EAEAEA;
  width: 90%;
  display: grid;
  border-radius: 0 0 0.25em 0.25em;
}
.NewTrips .MoreInfo .TripInformation {
  font-style: italic;
  padding: 3% 0 3% 10%;
  line-height: 30px;
}
.NewTrips .MoreInfo .Contact {
  text-align: center;
  line-height: 30px;
  margin: 0 0 2% 0;
}
.NewTrips .MoreInfo .Btn {
  background-color: #00904A;
  border-style: none;
  color: #FFFFFF;
  border-radius: 5px;
  padding: 10px 0;
  font-size: 18px;
  font-weight: 200;
  margin: 0 20px 10px 20px;
}